/******* header-mobile-menu*******/
.header-menu-open {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
}

.header-menu {
  z-index: -10;
  display: flex;
  position: absolute;
  // position: fixed;
  top: 0;
  right: 0;
  width: 374px;
  height: 812px;
  overflow-y: auto;

  background: radial-gradient(
      183.6% 183.01% at 59.76% -23.42%,
      #4f2ee8 18.03%,
      #ffffff 100%
    )
    /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  box-shadow: 0px 10px 10px rgba(245, 245, 247, 0.1);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-100%);
  transition: var(--timing-function);
  // transition-property: transform;
  transition-duration: 0.25s;

  //  @media screen  and (max-width: 768px)  {

  //    display: none;
  //  }
}
//  @media screen  and (max-width: 768px)  {
//   .header-menu-open {
//     display: none;
//   }
//   //  display: none;
//  }

.form-check > button.header-menu-open:relative {
  background-image: url('/src/images/sprite-header.svg#icon-align-left');
  display: none;
}
.form-check > button.header-menu-closed:active {
  display: none;
}
.form-check > button.header-menu-open:relative {
  background-image: url('/src/images/sprite-header.svg#icon-align-left');
  display: none;
}

.icon-sign {
  fill: #5046e5;
}

.header-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}
.header-menu-closed {
  position: absolute;
  top: 16px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border: none;
  padding: 0;
  background-color: transparent;

  transform: translateX(0);
  transition: stroke 250ms var(--timing-function);
}

/******* header-mobile-menu*******/

.page-header {
  z-index: 50;
  background: var(--color-white);
  position: fixed;
  width: 100%;
  opacity: 0.9;
}
.header-content {
  position: relative;
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0px 0px 8px 8px;
  background: var(--color-white);
  border: 1.5px solid #111111;
}
.logo {
  margin: 0;
  padding: 0;
}

.logo-bookshelf {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  align-items: center;
}
.form-check {
  margin: 0;
  padding: 0;
  gap: 14px;
  display: flex;
  align-items: center;
}
