}

.form-check > button.header-menu-open:relative {
  background-image: url("sprite-header.cab27a6e.svg#icon-align-left");
  display: none;
}

.form-check > button.header-menu-closed:active {
  display: none;
}

.form-check > button.header-menu-open:relative {
