.ui-switcher {
  height: 20px;
  width: 40px;
  box-sizing: border-box;
  vertical-align: middle;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  transition: border-color .25s;
  display: inline-block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.ui-switcher[aria-checked="true"] {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.ui-switcher:after {
  content: " ";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: left .25s;
  display: inline-block;
  position: absolute;
  top: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.ui-switcher[aria-checked="false"]:after {
  left: 1px;
}

.ui-switcher[aria-checked="true"]:after {
  left: 21px;
}

